#explorer_app {
  display: flex;
  flex-direction: row;
  justify-content: center;
  opacity: 0.95;
}

.explorer-sidebar {
  height: 100%;
  width: 25%;
  padding-top: 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
  opacity: 0.9;
}
.explorer-content {
  height: 100%;
  width: 75%;
  padding-top: 50px;
}
.explorer-sidebar-btns {
  width: 80%;
  opacity: 0.9;
  height: 67px;
  margin: 6px 0px;
  transition-duration: 0.3s;
  display: flex;
  align-items: center;
}
.btn-icon {
  height: 50px;
  width: 60px;
  background-size: 80%;
  background-repeat: no-repeat;
  background-position: center;
  background-color: transparent;
  border-radius: 7px;
  margin: 0px 10px;
  resize: none;
}
.btn-title {
  font-size: 18px;
}

#photos-icon {
  background-image: url("../images/Folder\ Yellow\ Photo.ico");
}
#audio-icon {
  background-image: url("../images/Folder\ Yellow\ Music.ico");
}

#videos-icon {
  background-image: url("../images/Folder\ Yellow\ Videos.ico");
}
#html-icon {
  background-image: url("../images/Folder\ Yellow\ Code.ico");
}
#python-icon {
  background-image: url("../images/Folder\ Yellow\ Python.ico");
}
#documents-icon {
  background-image: url("../images/Folder\ Yellow\ Microsoft\ Office.ico");
}
